<script>
	import { onMount } from 'svelte';
	import { buildFrequencies, closestFrequency } from './utils';
	import Tone from 'tone';
	export let name;

	const MAX_FREQ = 500;
	const MIN_FREQ = 30;
	const MAX_VOL = 0;
	const MIN_VOL = -5;

	const frequencies = buildFrequencies();
	const distortion = new Tone.Distortion(1).toMaster();
	const volume = new Tone.Volume(MIN_VOL).toMaster();
	const synth = new Tone.DuoSynth({
		oscillator: {
			type: "triangle"
		}
	}).toMaster();

	let canvas, context, button, prevButton;
	let countdown, countdownTimeout;
	let touches = {};
	let keys = [];
	let position = { x: 0, y: 0 };
	let size = {
		width: window.innerWidth,
		height: window.innerHeight,
	};
	let countdownTime = 5000; // milliseconds

	$: _position = {
		_x: position.x / size.width,
		_y: position.y / size.height,
	};
	$: buttonDown = (button === 0 || button === 2);
	$: frequency = closestFrequency(
		(MAX_FREQ * _position._x) + MIN_FREQ,
		frequencies
	);

	const handleKeydown = e => {
		keys = [...keys, e.key];
		console.log(e.key);
	}

	const handleKeyup = (e) => {
		keys = [...keys.slice(keys.indexOf(e.key))];
	}

	const performCountdown = () => {
		if (countdown <= 0) {
			window.clearTimeout(performCountdown);
			countdown = null;
		} else {
			setTimeout(performCountdown, 1000);
		}
	}

	const resizeCanvas = () => {
		size = {
			width: window.innerWidth,
			height: window.innerHeight,
		};
		canvas.width = size.width;
		canvas.height = size.height;
	}

	const setCursorPosition = event => {
		const { clientX, clientY } = event;
		const { left, top } = canvas.getBoundingClientRect();
		position = {
			x: clientX - left,
			y: clientY - top
		};
	};

	const setTouchPosition = touch => {
		position = { 
			x: touch.pageX,
			y: touch.pageY
		};
	}

	const mouseOut = () => {
		//button = null;
		synth.triggerRelease();
	};

	const unmount = (frame) => {
		cancelAnimationFrame(frame);
		window.removeEventListener('resize');
		window.removeEventListener('mouseout');
	};

	const mouseDown = e => {
		button = e.button;
		countdown = null;

		if (prevButton === null && button !== prevButton) {
			setCursorPosition(e);
			const { _x, _y } = _position;
			prevButton = button;
			synth.harmonicity.value = 1 + _y;
			synth.triggerAttack(frequency);
		}

		if (countdownTimeout) {
			window.clearTimeout(countdownTimeout);
		}
	};

	const move = e => {
		setCursorPosition(e);
		const { _x, _y } = _position;
		distortion.value = _y;
		synth.frequency.value = (MAX_FREQ * _x) + MIN_FREQ;
		synth.harmonicity.value = 1 + _y;

		if (buttonDown && countdownTimeout) {
			window.clearTimeout(countdownTimeout);
		}
	};

	const mouseUp = e => {
		button = null;

		if (prevButton !== null) {
			prevButton = button;
			synth.triggerRelease();
			countdownTimeout = window.setTimeout(() => countdown = countdownTime, 0);
		}
	};

	const touchStart = e => {
		const { changedTouches } = e;

		if (changedTouches.length) {
			const touch = changedTouches[0];
			distortion.value = touch.pageY;
			synth.frequency.value = (MAX_FREQ * touch.pageX) + MIN_FREQ;
			synth.harmonicity.value = 1 + touch.pageY;
			synth.triggerAttack(frequency);
		}

		if (countdownTimeout) {
			window.clearTimeout(countdownTimeout);
		}

		/*
		for (let i = 0; i < changedTouches.length; i += 1) {
			const touch = changedTouches[i];
			touches[touch.identifier] = {
				x: touch.pageX,
				y: touch.pageY
			};
		}
		*/
	};

	const touchMove = e => {
		const { changedTouches } = e;

		setCursorPosition(e);

		if (changedTouches.length) {
			const touch = changedTouches[0];
			distortion.value = touch.pageY;
			synth.frequency.value = (MAX_FREQ * touch.pageX) + MIN_FREQ;
			synth.harmonicity.value = 1 + touch.pageY;
		}

		if (changedTouches.length) {
			window.clearTimeout(countdownTimeout);
		}

		/*
		for (let i = 0; i < changedTouches.length; i += 1) {
			const touch = changedTouches[i];
			touches[touch.identifier] = {
				x: touch.pageX,
				y: touch.pageY
			};
			distortion.value = _y;
			synth.frequency.value = (MAX_FREQ * _x) + MIN_FREQ;
			synth.harmonicity.value = 1 + _y;
		}
		*/
	};

	const touchEnd = e => {
		const { changedTouches } = e;

		if (changedTouches.length) {
			synth.triggerRelease();
			countdownTimeout = window.setTimeout(() => countdown = countdownTime, 0);
		}

		/*
		for (let i = 0; i < changedTouches.length; i += 1) {
			delete touches[changedTouches[i].identifier];
		}
		*/
	};

	onMount(() => {
		resizeCanvas();
		window.addEventListener('resize', resizeCanvas, false);
		window.addEventListener('mouseout', mouseOut, false);

		context = canvas.getContext('2d');

		let frame;

		(function loop() {
			const { x, y } = position;
			const { _x, _y } = _position;
			const t = window.performance.now();

			const r = Math.round(
				64 + (125 * _x) + (64 * Math.sin(_x))
			);
			const g = Math.round(
				64 + (125 * (1 - _y)) + (64 * Math.cos(_y))
			);
			const b = Math.round(
				64 + (125 * _y) + (64 * Math.cos(_y))
			);
			context.fillStyle = `rgb(${r},${g},${b})`;
			context.fillRect(0, 0, canvas.width, canvas.height);

			frame = requestAnimationFrame(loop);

			if (buttonDown && button === prevButton) {
				context.beginPath();
				context.fillStyle = 'white';
				context.arc(x, y, 20, 0, 2 * Math.PI);
				context.fill();
			} else {
				context.beginPath();
				context.fillStyle = 'rgba(255, 255, 255, 0.2)';
				context.arc(x, y, 20, 0, 2 * Math.PI);
				context.fill();
			}

			if (countdown !== null && countdown >= 0) {
				const width = 200 * (countdown / countdownTime) 
				context.fillRect(size.width - 220, 20, width, 20);
				countdown -= (countdownTime / 120);
			}
		}());

		return () => unmount(frame);
	});

</script>

<svelte:window
	on:keydown={handleKeydown}
	on:keyup={handleKeyup}
/>
<canvas
	bind:this={canvas}
	on:mousedown|preventDefault={mouseDown}
	on:mouseup|preventDefault={mouseUp}
	on:mousemove|preventDefault={move}
	on:contextmenu|preventDefault={mouseUp}
	on:touchstart|preventDefault={touchStart}
	on:touchmove|preventDefault={touchMove}
	on:touchend|preventDefault={touchEnd}
>
</canvas>